<template lang="pug">
    v-card
        v-toolbar(flat)
            v-toolbar-title 注水构成曲线
            v-spacer
            v-btn(color="success" @click="update_line_data") 更新曲线
        
        v-divider
       
       
        div(ref="injeCompElement" class="injeCompClass")
            VegaChartView(
                :key="chartId"
                :id="chartId"
                :chartId="chartIdStr"
                :chartSource="prodCompData.injeCompData"
                :chartNum="chartNum"
            )
</template>

<script>

import { get } from 'vuex-pathify'
import prodCompData from "./ProdCompData.js";
import * as api from "@/apis/wswebapi"
export default {
  name: "InjeCompLine",
  data: () => ({
        prodCompData: prodCompData,
        chartNum:0,
        title:"",
        titles:[{text:"产油量",value:"MOIL"},{text:"产液量",value:"MLIQUID"},{text:"产水量",value:"MWATER"}],
        chartIdStr:"#InjeCompLine",
        chartId:"InjeCompLine",
  }),
  computed:{
      screenWidth:get("app/screenWidth"),
      blockname:get("peproject/blockname"),
    //   chartId(){
    //       let cid = Math.random().toString(36).slice(-8);
    //       this.setChartId(cid);
    //       return cid 
    //   }
  },
  methods:{
      //   更新曲线
    update_line_data(){
        api.GetProdCompData({blockname:this.blockname,collection:"waterreportmonth",title:"MIWATER"}).then(res=>{
            if (res.code==200){
                this.prodCompData.injeCompData.datasets.injeCompData=res.data;
                this.chart();
            }else{
                console.log("返回错误",res.code)
                this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true});
            }
        })
    },
      setChartId(){
          let cid = Math.random().toString(36).slice(-8);
          this.chartId=cid;
          this.chartIdStr="#"+cid;
          console.log("InjeCompLine ",this.chartId,this.chartIdStr);
      },
      chart(){
          console.log("宽度:",this.$refs.injeCompElement.clientWidth);
          this.prodCompData.injeCompData.width=this.$refs.injeCompElement.clientWidth-150;
          this.chartNum=this.chartNum+1;
      }
  },


    watch:{
        screenWidth(){
            this.setChartId();
            setTimeout(this.chart,2000);
        }
    },
    components: {
      VegaChartView: () => import('@/views/charts/vegacharts/VegaChartView'),
    },

};
</script>

<style>
.injeCompClass{
    width:100%
}
</style>